@import ../../styles/helpers

 

.item
    display: none
    &:first-child
        display: block

.title
    text-align: center

.info
    margin-top: 12px
    text-align: center
    +body-2
    color: $n4

.btns
    display: flex
    justify-content: center
    margin-top: 32px
    .button
        background: $white
        color: $n1
        border: 1px solid $n9 
        border-radius: 8px  
       
        &:not(:last-child)
            margin-right: 12px
            
.btns    
    .button
         img
           height: 22px
           margin-right: 18px



.note
    margin-top: 32px
    text-align: center
    +caption-2
    color: $n4

.form
    margin-top: 32px
    & > .button
        width: 100%
        margin-top: 12px

.form   
    input
        border-radius: 12px

.foot
    margin-top: 32px
    text-align: center
    +caption-2
    color: $n3
    +dark
        color: $n6

.link,
.password
    font-weight: 600
    transition: color .2s

.link
    color: $p1
    &:hover
        color: darken($p1, 10)

.password
    color: $n3
    &:hover
        color: $n1
    +dark
        color: $n6
        &:hover
            color: $p1

.variants
    display: flex
    flex-direction: column
    margin-bottom: 20px

.radio
    &:not(:last-child)
        margin-bottom: 16px
        padding-bottom: 16px
        border-bottom: 1px solid $n6
        +dark
            border-color: $n3

.code
    display: flex
    justify-content: center

.number
    flex-shrink: 0
    width:60px
    input
        width: 100%
        height: 55px
        width: 55px
        border-radius: 100%
        border: 1px solid $n10
        background: $white
        text-align: center
        +poppins
        font-size: 18px
        font-weight: 400
        color: $n2
        transition: all .2s
        &:focus
            border:2px solid $p5
            background: $white
        +dark
            background: $n2
            border-color: $n2
            color: $n8
            &:focus
                background: none
                border-color: $n3
    &:not(:last-child)
        margin-right: 16px

.textStyle
    margin-top:20px
    margin-bottom: 20px
    font-size: 14px
    font-family: "Poppins"

    